<template>
  <Form ref="loginForm" :model="form" :rules="rules" @keydown.enter.native="handleSubmit">
    <FormItem prop="userName">
      <Input v-model="form.userName" placeholder="请输入用户名">
        <span slot="prepend">
          <Icon :size="16" type="ios-person"></Icon>
        </span>
      </Input>
    </FormItem>
    <FormItem prop="password">
      <Input type="password" v-model="form.password" placeholder="请输入密码">
        <span slot="prepend">
          <Icon :size="14" type="md-lock"></Icon>
        </span>
      </Input>
    </FormItem>
    <FormItem>
      <Button @click="handleSubmit" type="primary" long>登录</Button>
    </FormItem>
    <FormItem>
      <span style="float:left">
        <Checkbox v-model="checked" @on-change="remember">记住密码</Checkbox>
        <!-- <input
          type="checkbox"
          value="remember-me"
          id="remember-password-checkbox"
          v-model="val"
          @click="changeVal"
        />
        <label for="remember-password-checkbox" style="color:grey;margin-left:5px">记住密码</label>-->
      </span>

      <span style="float:right;color:#2f6e6a;cursor:pointer">忘记密码?</span>
    </FormItem>
  </Form>
</template>
<script>
export default {
  name: "LoginForm",
  props: {
    userNameRules: {
      type: Array,
      default: () => {
        return [{ required: true, message: "账号不能为空", trigger: "blur" }];
      }
    },
    passwordRules: {
      type: Array,
      default: () => {
        return [{ required: true, message: "密码不能为空", trigger: "blur" }];
      }
    }
  },
  data() {
    return {
      // val: false,
      checked: false,
      form: {
        userName: "",
        password: ""
      }
    };
  },
  computed: {
    rules() {
      return {
        userName: this.userNameRules,
        password: this.passwordRules
      };
    }
  },
  //页面加载调用获取cookie值
  mounted() {
    this.getCookie();
  },
  methods: {
    remember() {
      // console.log(this.checked);
      this.setCookie(this.checked, 7);
    },
    handleSubmit() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.$emit("on-success-valid", {
            userName: this.form.userName,
            password: this.form.password
          });
        }
      });
    },
    // 父组件获取数据
    getVal() {
      return this.checked;
    },
    //读取cookie
    getCookie: function() {
      if (document.cookie.length > 0) {
        var arr = document.cookie.split("; "); //这里显示的格式需要切割一下自己可输出看下
        // console.log(arr);
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split("="); //再次切割
          // console.log(arr2);
          //判断查找相对应的值
          if (arr2[0] == "userName") {
            this.form.userName = arr2[1]; //保存到保存数据的地方
          } else if (arr2[0] == "userPwd") {
            this.form.password = arr2[1];
          } else if (arr2[0] == "checkedStatus") {
            this.checked = JSON.parse(arr2[1]);
            // console.log(JSON.parse(this.checked));
          }
        }
      }
    },
    //设置checked的cookie
    setCookie(c_status, exdays) {
      var exdate = new Date(); //获取时间
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //保存的天数
      //字符串拼接cookie
      window.document.cookie =
        "checkedStatus" +
        "=" +
        c_status +
        ";path=/;expires=" +
        exdate.toGMTString();
    }
  }
};
</script>

<style lang="less" scoped>
.ivu-btn-primary {
  background-color: #2f6e6a;
  border-color: #2f6e6a;
}
</style>