<style lang="less">
@import "./login.less";
</style>

<template>
  <div class="login">
    <div class="login-con">
      <Card icon="log-in" title="用户登录" :bordered="false">
        <div class="form-con">
          <login-form @on-success-valid="handleSubmit" ref="child"></login-form>
        </div>
      </Card>
    </div>
    <img src="../assets/标题.png" alt class="title" />
    <!-- <img src="../assets/白色背景.png" alt="" class="bgwhite"> -->
  </div>
</template>

<script>
import LoginForm from "../components/login-form/index";
import qs from "qs";
export default {
  components: {
    LoginForm
  },
  methods: {
    handleSubmit({ userName, password }) {
      let checked = this.$refs["child"].getVal();
      console.log(checked);
      let params = {
        username: userName,
        password: password
      };
      this.$axios.post("/sys/login", qs.stringify(params)).then(res => {
        // console.log(res);
        if (res.data.code == 0) {
          const self = this;
          //判断复选框是否被勾选 勾选则调用配置cookie方法
          if (checked == true) {
            console.log("checked == true");
            //传入账号名，密码，和保存天数3个参数
            self.setCookie(userName, password, 7);
          } else {
            console.log("清空Cookie");
            //清空Cookie
            self.clearCookie();
          }
          this.$Message.success(res.data.data);
          this.$router.push({ name: "About" });
        } else {
          this.$Message.error(res.data.data);
        }
      });
    },
    //设置cookie
    setCookie(c_name, c_pwd, exdays) {
      var exdate = new Date(); //获取时间
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //保存的天数
      //字符串拼接cookie
      window.document.cookie =
        "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
      window.document.cookie =
        "userPwd" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
    },

    //清除cookie
    clearCookie: function() {
      this.setCookie("", "", -1); //修改2值都为空，天数为负1天就好了
    }
  }
};
</script>

